import { VlocityInput } from "../../datasource/request/VlocityInput";

export class FetchTranslationsInput extends VlocityInput {
  public textToTranslate?: string[];
  public language: string;
  public domain: string;
  public sobjectFieldNames?: string[];
  public sobjectNames?: string[];

  /**
   * optional input param used as secure Server rest URL end point when Proxy SDK is used
   */
  public secureServerEndPointUrl?: string;
  /*
   * content type of the resource.
   */
  public contentType?: string = "application/json";

  constructor() {
    super();
    this.remoteClass = "ProductConsoleControllerV2";
    this.remoteMethod = "invokeMethod";
    this.apexRestUrl = "/v1/usercustomlabels";
    this.apexRestUrlAWS = "/v3/usercustomlabels";
  }

  /**
   * Construct the paramsArray for ApexRemote call.
   * @returns {Array} array of args to be supplied to ApexRemote Call.
   * The sequence of the element corresponds to the arguments of the Apex @RemoteAction method.
   */
  public paramsArray() {
    let paramsArray = {
      language: this.language
    };
    if (this.textToTranslate) {
      paramsArray["labelNames"] = this.uriEncodedTextToTranslate(
        this.textToTranslate
      );
    }

    if (this.sobjectFieldNames) {
      paramsArray["sobjectFieldNames"] = this.uriEncodedTextToTranslate(
        this.sobjectFieldNames
      );
    }
    if (this.sobjectNames) {
      paramsArray["sobjectNames"] = this.uriEncodedTextToTranslate(
        this.sobjectNames
      );
    }
    return ["getCustomLabels", JSON.stringify(paramsArray)];
  }

  /**
   * URI Encoded each label (in this.textToTranslated) to be translated and join them into a comma separated string.
   * @return {string} comma separated string of labels to be translated.
   */
  public uriEncodedTextToTranslate(labels): string {
    return labels.map(label => `${encodeURIComponent(label)}`).join();
  }
}
