import * as LZString from "lz-string";
import { BaseSDK, HttpMethod } from "../common";
import { ApexRemoteInput } from "../datasource/apexremote";
import { ApexRestInput } from "../datasource/apexrest";
import { RestInput } from "../datasource/rest/RestInput";
import { isNode } from "../datasource/utils/isNode";
import { Logger } from "../logger/logger";
import { FetchTranslationsInput } from "./common/FetchTranslationsInput";
import TranslationSDKConfig from "./TranslationSDKConfig";
const translationDBName: string = "TranslationDB";
const supportsLocalStorage = !isNode && "localStorage" in window;
/**
 * TranslationService is the Service responsible for translation.
 * Current implmenetation supports SalesForce Custom Labels and Dictionaries.
 */
export default class Translation extends BaseSDK {
  /**
   * Get a singleton instance of this Translation.
   * @param {config} TranslationSDKConfig to initialize this SDK.
   * @returns an instance of this SDK.
   */
  public static getInstance(config: TranslationSDKConfig): Translation {
    // If there is no instance and no datasource to create the instance, throw an error.
    if (!Translation.instance && (!config || !config.datasource)) {
      throw new Error(
        "TranslationService:getInstance: TranslationService cannot be instantiated properly. TranslationSDKConfig and TranslationSDKConfig.datasource must be provided."
      );
    }
    if (!Translation.instance || (config && config.create)) {
      Translation.instance = new Translation(config);
    }
    return Translation.instance;
  }

  private static instance: Translation;
  private locale: string;
  /**
   * The label storage.
   * This will used when there is no Local storage
   */
  private labelStorage: Map<string, string> = new Map<string, string>();

  /**
   * The config used to initialize this SDK.
   */
  private config: TranslationSDKConfig;
  private dictionary: {};

  constructor(config: TranslationSDKConfig) {
    super();
    this.config = config;
    return;
  }
  /**
   * Returns the number of labels in labelStorage.
   * @returns {number} - count of labels cached in labelStorage.
   */
  public labelStorageCount(): number {
    return this.labelStorage.size;
  }

  /**
   * Clear the label storage. This will remove all labels in storage.
   * @returns {boolean} true on successfully clearing the label storage.
   */
  public clearLabelStorage(): boolean {
    this.labelStorage.clear();
    return true;
  }

  /**
   * Return the labels in cache as a JSON Object with label key as the key and the custom label as the value.
   * @returns {object} JSON objec to label keys and translated labels as values.
   */
  public getLabelsInCache(): object {
    const obj = Object.create(null);
    for (const [k, v] of this.labelStorage) {
      obj[k] = v;
    }
    return obj;
  }

  /**
   * Create an input object of the FetchTranslationInput.
   * @returns {FetchTranslationInput} - input object that contains the labels needs to be fetched.
   */
  public createFetchTranslationsInput(): FetchTranslationsInput {
    return new FetchTranslationsInput();
  }

  /**
   * This function fetch the translations for a given set of label(s) with the language provided.
   * @param {FetchTranslationsInput} - input object that contains the labels to be translated and the language to translate to.
   * @return {Promise<Result>} - promise that will get all the translated labels and keep them in labelStorage.
   */
  public async fetchTranslations(input: FetchTranslationsInput): Promise<any> {
    this.locale = input.language;
    await this.getLabelsNotInCache(input);
    if (
      (!input.textToTranslate || !input.textToTranslate.length) &&
      (!input.sobjectFieldNames || !input.sobjectFieldNames.length) &&
      (!input.sobjectNames || !input.sobjectNames.length)
    ) {
      return Promise.resolve();
    }
    input.textToTranslate &&
      input.textToTranslate.length &&
      Logger.info("Fetching labels: " + input.textToTranslate);
    input.sobjectFieldNames &&
      input.sobjectFieldNames.length &&
      Logger.info("Fetching sobject field labels: " + input.sobjectFieldNames);
    input.sobjectNames && input.sobjectNames.length;
    Logger.info("Fetching sobject labels: " + input.sobjectNames);
    if (this.config.anonymousUserUrl) {
      return this.fetchAnonymousUserTranslation(input);
    }
    if (this.config.datasource.config.useApexRemoteForDualDataSource) {
      const remoteInput: ApexRemoteInput = new ApexRemoteInput({
        className: input.remoteClass,
        methodName: input.remoteMethod,
        paramsArray: input.paramsArray(),
        cacheable: this.config.cacheable,
        cacheExpiration: this.config.cacheExpiration,
        reload: this.config.reload
      });
      return await this.config.datasource
        .apexRemote(remoteInput)
        .execute()
        .then(async result => {
          if (!result) {
            throw new Error("Invalid result: " + result);
          }
          await this.manipulateAndStoreLabelData(result.data);
        });
    } else {
      let path = `${input.getApexRestEndPoint()}?language=${input.language}`;
      path = this.getQueryStringParameters(path, input);
      const restInput: ApexRestInput = new ApexRestInput({
        method: HttpMethod.GET,
        path,
        cacheable: this.config.cacheable,
        cacheExpiration: this.config.cacheExpiration,
        reload: this.config.reload
      });
      input.apexRestUrl = "/v1/usercustomlabels";
      await this.config.datasource
        .apexRest(restInput)
        .execute()
        .then(async result => {
          Logger.info("data: " + JSON.stringify(result.data));
          await this.manipulateAndStoreLabelData(result.data);
        });
    }
    input.domain && (await this.getTranslationDictionary(input, 1, ""));
  }

  /**
   * Get the corresponding translated string for a given label.
   * fetch() must be called first before calling this function.
   * This function translates only strings exist in local Storage/this.labelStorage. Otherwise, it will return the provided label.
   * @param {label} - to be translated.
   * @returns {string} - translatedLabel that is translated if exists. Otherwise, return the provided label.
   */
  public translate(label: string): string {
    let translatedLabel;
    if (supportsLocalStorage) {
      translatedLabel = JSON.parse(
        LZString.decompressFromUTF16(
          localStorage.getItem(translationDBName + this.locale)
        )
      )[label];
    } else {
      translatedLabel = this.labelStorage.get(label);
    }
    if (translatedLabel) {
      return translatedLabel;
    }

    // return original label if translation not found.
    return label;
  }

  /**
   * Check the input.textToTranslate contains labels that are already in labelStorage.
   * If already exists in labelStorage, remove it from input.textToTranslate.
   * @param {input} - FetchTranslationsInput which non cached labels to be fetched.
   * @returns {Promise<FetchTranslationsInput>} - Promise with updated input.textToTranslate.
   */
  private async getLabelsNotInCache(
    input: FetchTranslationsInput
  ): Promise<FetchTranslationsInput> {
    const labelsToTranslate = input.textToTranslate;
    const labelsNotInCache: string[] = [];
    // if labelsToTranslate is not an array OR length is 0
    if (!Array.isArray(labelsToTranslate) || !labelsToTranslate.length) {
      input.textToTranslate = labelsNotInCache;
      return Promise.resolve(input);
    }
    if (supportsLocalStorage) {
      const labelsInStorage = this.getLabelsFromLocalStorage();
      if (!labelsInStorage) {
        input.textToTranslate = labelsToTranslate;
        return Promise.resolve(input);
      }
      for (const label of labelsToTranslate) {
        if (!labelsInStorage.hasOwnProperty(label)) {
          // Labels not in cache
          labelsNotInCache.push(label);
        }
      }
    } else {
      if (!this.labelStorage) {
        input.textToTranslate = labelsToTranslate;
        return Promise.resolve(input);
      }
      for (const label of labelsToTranslate) {
        if (!this.labelStorage.has(label)) {
          // Labels not in cache
          labelsNotInCache.push(label);
        }
      }
    }
    input.textToTranslate = labelsNotInCache;
    return Promise.resolve(input);
  }

  /**
   * Store translated labels in cache.
   * @param {labelsData} labels retrieved from SalesForce.
   * @return {number} number of labels stored.
   */
  private storeLabelsInCache(labelsData: object): number {
    let labelCount: number = 0;

    if (!labelsData) {
      return labelCount;
    }
    for (const label in labelsData) {
      if (labelsData.hasOwnProperty(label)) {
        this.labelStorage.set(label, labelsData[label]);
        labelCount++;
      }
    }

    return labelCount;
  }
  private async getTranslationDictionary(
    input: FetchTranslationsInput,
    index: number,
    eTag: string
  ): Promise<any> {
    const dictionaryInput = Object.create(input);
    dictionaryInput.apexRestUrl = "/v2/stringtranslations/dictionary";
    const restInput: ApexRestInput = new ApexRestInput({
      method: HttpMethod.GET,
      path: `${dictionaryInput.getApexRestEndPoint()}/${
        dictionaryInput.domain
      }/${dictionaryInput.language}?page=${index}`,
      options: {
        headers: {
          "If-None-Match": eTag
        }
      },
      cacheable: this.config.cacheable,
      cacheExpiration: this.config.cacheExpiration,
      reload: this.config.reload
    });
    await this.config.datasource
      .apexRest(restInput)
      .execute()
      .then(async response => {
        if (
          response &&
          response.records &&
          response.records[0].dictionary &&
          Object.keys(response.records[0].dictionary).length > 0
        ) {
          this.dictionary = {
            ...this.dictionary,
            ...response.records[0].dictionary
          };
          return this.getTranslationDictionary(
            input,
            index + 1,
            response.records[0].eTag
          );
        } else {
          await this.storeDataInCache(this.dictionary);
          return Promise.resolve();
        }
      });
  }
  private manipulateAndStoreLabelData(data) {
    if (!data) {
      return;
    }

    // Remove unecessary keys
    let dataKeyToBeDeleted = "language";
    delete data[dataKeyToBeDeleted];
    dataKeyToBeDeleted = "totalSize";
    delete data[dataKeyToBeDeleted];
    return this.storeDataInCache(data);
  }

  private async storeDataInCache(data) {
    if (supportsLocalStorage) {
      return this.setDataToLocalStorage(data);
    } else {
      return this.storeLabelsInCache(data);
    }
  }
  private setDataToLocalStorage(data) {
    let localStorageVal = localStorage.getItem(translationDBName + this.locale);
    try {
      localStorageVal = JSON.parse(
        LZString.decompressFromUTF16(localStorageVal)
      );
    } catch (e) {
      localStorage.removeItem(translationDBName + this.locale);
      return this.handleLocalStorage({}, data);
    }
    return this.handleLocalStorage(localStorageVal, data);
  }

  private getLabelsFromLocalStorage() {
    let localStorageVal: any = localStorage.getItem(
      translationDBName + this.locale
    );
    try {
      localStorageVal = JSON.parse(
        LZString.decompressFromUTF16(localStorageVal)
      );
    } catch (e) {
      localStorageVal = null;
    }
    return localStorageVal;
  }

  private getQueryStringParameters(path, input) {
    if (input.textToTranslate && input.textToTranslate.length) {
      path += `&names=${input.uriEncodedTextToTranslate(
        input.textToTranslate
      )}`;
    }
    if (input.sobjectFieldNames && input.sobjectFieldNames.length) {
      path += `&sobjectFieldNames=${input.uriEncodedTextToTranslate(
        input.sobjectFieldNames
      )}`;
    }
    if (input.sobjectNames && input.sobjectNames.length) {
      path += `&sobjectNames=${input.uriEncodedTextToTranslate(
        input.sobjectNames
      )}`;
    }
    return path;
  }
  private async fetchAnonymousUserTranslation(input) {
    let path;
    if (
      this.config.anonymousUserUrlType &&
      this.config.anonymousUserUrlType === "AWSEndPoint"
    ) {
      path = `${input.apexRestUrlAWS}?language=${input.language}`;
    } else {
      path = `${input.apexRestUrl}?language=${input.language}`;
    }

    path = this.getQueryStringParameters(path, input);
    let restOptions: RestInput;
    restOptions = new RestInput({
      options: {
        method: HttpMethod.GET,
        headers: {
          Authorization:
            this.getAuthorisationType() + " " + this.config.authToken
        }
      },
      url: this.config.anonymousUserUrl + path,
      cacheable: this.config.cacheable,
      reload: this.config.reload,
      cacheExpiration: this.config.cacheExpiration
    });
    return await this.config.datasource
      .rest(restOptions)
      .execute()
      .then(async result => {
        if (!result) {
          throw new Error("Invalid result: " + result);
        }
        await this.manipulateAndStoreLabelData(result.data);
      });
  }
  private getAuthorisationType() {
    if (this.config.authTokenType) {
      return this.config.authTokenType;
    } else {
      return "Basic";
    }
  }
  private handleLocalStorage(localStorageVal, data) {
    if (localStorageVal && localStorageVal !== "undefined") {
      localStorageVal = { ...localStorageVal, ...data };
      localStorage.removeItem(translationDBName + this.locale);
      localStorage.setItem(
        translationDBName + this.locale,
        LZString.compressToUTF16(JSON.stringify(localStorageVal))
      );
      return Object.keys(localStorageVal).length;
    } else {
      localStorage.setItem(
        translationDBName + this.locale,
        LZString.compressToUTF16(JSON.stringify(data))
      );
      return Object.keys(data).length;
    }
  }
}
